<template>
    <div class="module-info">
        <div class="field-part">
            <slot name="item"></slot>
        </div>

        <div class="field-part">
            <cell-map :dataCopy="infoList" :map="map"></cell-map>
        </div>

        <div class="field-part">
            <mt-cell title="缴费金额">
                <span class="readonly-amount">{{infoList.money | formatNumber(2)}}元</span>
            </mt-cell>
        </div>

    </div>
</template>

<script>
    export default {
        name: 'module-info',
        props: ['module'],
        data() {
            return {
                infoList: null,
                map: [
                    {
                        key: 'companyName',
                        title: '供电公司'
                    },
                    {
                        key: 'no',
                        title: '用户编号'
                    },
                    {
                        key: 'name',
                        title: '用户姓名'
                    },
                    {
                        key: 'money',
                        type: 'amount-unit',
                        title: '应缴金额'
                    }
                ]
            };
        },
        created() {
            let ls = this.$ls.getItem(this.module);
            this.infoList = ls;
        }
    };

</script>

<style lang="scss" scoped>
    .readonly-amount {
        font-size: 20px;
        color: #FF942F;
        letter-spacing: 0;
    }
</style>


