<template>
    <div class="life">
        <!-- 社区e银行入口 -->
        <div class="page-entry">

        </div>

        <div class="top">
            <p class="title">生活缴费</p>
            <div class="manager">
                <router-link to="history" tag="p" append>
                    记录
                </router-link>
                <p>管理</p>
            </div>
        </div>

        <div class="module-wrap">
            <template v-for="item in options">
                <div class="module" @click="doLife(item)">
                    <div class="icon"></div>
                    <div class="wrap">
                        <p class="title">{{item.title}}</p>
                        <p class="add" v-if="item.add">可添加</p>
                    </div>

                </div>
            </template>
        </div>

        <!-- 按钮 -->
        <div class="field-action">
            <mt-button type="default" size="large" @click="getMore">更多缴费</mt-button>
        </div>

        <life-more ref="more" :options="options"></life-more>
    </div>
</template>

<script>
    import * as services from '@/services/life';
    import LifeMore from './life-more';
    export default {
        name: 'life',
        components: {
            LifeMore
        },
        data() {
            return {
                cellList: null,
                options: [      // 更多缴费
                    { title: '电费', link: 'LifePower', add: true },
                    { title: '水费', link: 'LifeWater', add: true },
                    { title: '话费充值', link: 'LifePhone' },
                    { title: '燃气费', link: 'LifeGas', add: true },
                    { title: '有线电视费', link: 'LifeTelevision', add: true },
                    { title: '热力费', link: 'LifeHeat', add: true },
                    { title: '交通罚款', link: 'LifeTraffic' },
                    { title: '学费', link: 'LifeSchool' }
                ]
            };
        },
        created() {
            /** 获取cell数据 **/
            services.getLifeCell()
                .then(response => {
                    this.cellList = response.data;
                });
        },
        methods: {
            // 更多缴费
            getMore() {
                this.$refs.more.show();
            },
            doLife(item) {
                this.$router.push({ name: item.link });
            }
        }
    };

</script>

<style lang="scss" scoped>
    .page-entry {
        height: 240px;
        @include image-set('../../assets/img/life/banner');
        background-repeat: no-repeat;
        background-position: center;
        background-size: 100% 100%;
    }

    .top {
        padding: 32px 34px;

        .title {
            float: left;
            font-size: 14px;
            color: #272727;
        }

        .manager {
            float: right;
            padding: 0 16px;

            p {
                display: inline-block;
                margin: 0 20px;
                font-size: 13px;
                color: #308CF8;
            }
        }
    }

    .module-wrap {
        margin: 24px 30px 30px;
        box-sizing: border-box;
        @include clearfix();

        .module {
            float: left;
            margin: 10px 0;
            padding: 22px 28px;
            height: 140px;
            width: 49%;
            background: #FFFFFF;
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.03);
            border-radius: 5px;

            .icon {
                display: inline-block;
                width: 44px;
                height: 44px;
                background-image: url('../../assets/img/life/power@2x.png');
                background-repeat: no-repeat;
                background-position: center;
                background-size: 100% 100%;
                vertical-align: top;
            }

            .wrap {
                display: inline-block;
                .title {
                    font-size: 16px;
                    color: #272727;
                }

                .add {
                    font-size: 13px;
                    color: #999999;
                }
            }
        }

        .module:nth-child(2n+2) {
            margin-left: 2%;
        }
    }
</style>


