<template>
    <div class="heat-info">
        <module-info ref="child" module="heat">
            <template slot="item">
                <mt-cell title="缴费项目" value="热力费"></mt-cell>
            </template>
        </module-info>
        
        <div class="field-action">
            <mt-button type="primary" size="large" @click="pay">立即交费</mt-button>
        </div>
    </div>
</template>

<script>
    import ModuleInfo from '../module/info';
    export default {
        name: 'heat-info',
        components: {
            ModuleInfo
        },
        mounted() {
            this.$refs.child.map.companyName = "热力公司";
        },
        methods: {
            pay() {
                this.$router.push({ name: 'LifeHeatPay' });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] !== from.meta['groupName'] && to.meta['groupName'] !== 'selector') {
                this.$ls.removeItem('heat');
            }
            next(true);
        }
    };

</script>


