<template>
    <div class="phone">
        <div class="field-group">
            <mt-field placeholder="请输入手机号码" type="tel" v-model="phone.call" :attr="{ maxlength: 11 }"></mt-field>
            <i v-if="callEmpty" class="icon" @click="addres"></i>
            <div class="yys-wrap" :error="{'error': error}">
                <p v-text="phone.yys"></p>
            </div>
        </div>
        <div class="phone-amount">
            <div class="cell-top">
                选择充值金额
            </div>
            <div class="amount-pannel-group">
                <div v-for="amount in amounts" class="amount-pannel" :class="{'active': active === amount.id}" @click="selectAmount(amount)">
                    <p class="val">{{amount.val}}</p>
                    <p class="desc">{{amount.desc}}</p>
                </div>
            </div>
        </div>

        <div class="field-reminder">
            <h4>
                温馨提示：
            </h4>
            <p>
                1、业务缴费受理时间为01:00-23:00,请您合理安排缴费时间，由此带来的不便，敬请谅解！
            </p>
            <p>
                2、通过电子渠道缴费不支持发票凭证打印功能。
            </p>
        </div>

        <div class="field-action">
            <mt-button type="primary" :disabled="!clickable" size="large" @click="recharge">充值</mt-button>
        </div>
    </div>
</template>

<script>
    import ModuleIndex from '../module/index';
    export default {
        name: 'phone',
        data() {
            return {
                phone: {
                    call: '',
                    yys: '中国联通', // 显示运营商（可能包括姓名）
                    amount: ''
                },
                error: false,          // 错误提示
                active: '',         // 当前激活金额
                clickable: false,   // 是否可以点击
                callEmpty: true,    // 手机输入框是否为空
                amounts: [
                    {
                        id: '30',
                        val: '30元',
                        desc: '售价30元'
                    }, {
                        id: '50',
                        val: '50元',
                        desc: '售价50元'
                    }, {
                        id: '100',
                        val: '100元',
                        desc: '售价100元'
                    }, {
                        id: '200',
                        val: '200元',
                        desc: '售价200元'
                    }, {
                        id: '300',
                        val: '300元',
                        desc: '售价300元'
                    }, {
                        id: '500',
                        val: '500元',
                        desc: '售价500元'
                    }
                ]
            };
        },
        created() {
            let ls = this.$ls.getItem('phone');
            if (ls !== null && ls !== undefined) {
                this.phone.call = ls.call;
            }
        },
        watch: {
            'phone': {
                handler: 'validate',
                deep: true
            }
        },
        methods: {
            selectAmount(item) {
                this.phone.amount = item.id;
                this.active = item.id;
            },
            validate(val) {
                if (this.$validate.isEmpty(val.call)) {
                    this.clickable = false;
                    this.callEmpty = true;
                    return false;
                } else {
                    this.callEmpty = false;
                }
                if (!this.$validate.isPhone(val.call)) {
                    this.clickable = false;
                    return false;
                }
                if (this.$validate.isEmpty(val.amount)) {
                    this.clickable = false;
                    return false;
                }
                this.clickable = true;
                // if (val.length === 11) {
                //     if (!this.$validate.isPhone(val)) {
                //         this.phone.yys = "手机号码格式错误";
                //     } else {
                //         this.phone.yys = '中国移动';
                //     }
                // }
                // if (val.length < 11) {
                //     this.phone.yys = "";
                // }
                // this.phone.call = val.substring(0, 11);
            },
            recharge() {
                this.$router.push({ name: 'LifePhonePay' });
            },
            addres() {
                this.$msg('调用系统通讯录', 'mintui mintui-success');
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] === from.meta['groupName'] || to.meta['groupName'] == 'selector') {
                this.$ls.setItem('phone', this.phone);
            } else {
                this.$ls.removeItem('phone');
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>
    .field-group {
        position: relative;
        margin-bottom: 40px;
        background-color: #fff;


        .icon {
            position: absolute;
            top: 12px;
            right: 32px;
            height: 72px;
            width: 72px;
            background-image: url('../../../assets/img/selector/icon-address-book.png');
            background-size: 100% 100%;
            background-position: center center;
        }

        .yys-wrap {
            padding: 20px 50px;
        }

        .error {
            color: #FF511A;
            ;
        }
    }

    .phone-amount {
        position: relative;
        background-color: #fff;
        padding: 30px 50px;

        .cell-top {
            font-size: 13px;
            color: #666666;
            letter-spacing: 0;
        }

        .amount-pannel-group {
            margin-top: 14px;

            .amount-pannel {
                display: inline-block;
                margin: 14px 1%;
                padding: 14px 44px;
                width: 31.333%;
                text-align: center;
                border: 1px solid #ddd;
                border-radius: 3px;

                .val {
                    font-size: 16px;
                    color: #272727;
                }

                .desc {
                    margin-top: 10px;
                    font-size: 13px;
                    color: #666666;
                    letter-spacing: 0;
                }
            }
            p {
                white-space: nowrap;
            }
        }

        .active {
            border: 1px solid #FF942F!important;

            .val {
                color: #FF942F!important;
            }

            .desc {
                color: #FF942F!important;
            }
        }
    }
</style>


