<template>
    <div class="power-do-pay">
        <module-pay ref="child" :warn="warn" module="power">
            <mt-cell slot="item" title="缴费项目" value="电费"></mt-cell>
        </module-pay>

        <div class="field-action">
            <mt-button type="primary" size="large" @click="doPay">立即支付</mt-button>
        </div>

        <dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
    </div>
</template>

<script>
    import ModulePay from '../module/pay';
    export default {
        name: 'power-do-pay',
        components: {
            ModulePay
        },
        data() {
            return {
                warn: '备注名称例如：妈妈家的电费',
                dialogSmsVerify: {
                    title: '输入短信验证码',
                    btnConfirmText: '完成支付',
                    btnGetText: '获取验证码'
                }
            };
        },
        methods: {
            doPay() {
                let _data = Object.assign({}, this.$refs.child.formData);
                if (this.$validate.isEmpty(_data.account)) {
                    this.$msg('请选择支付账号');
                    return false;
                }
                if (this.$validate.isEmpty(_data.pwd)) {
                    this.$msg('请输入交易密码');
                    return false;
                }
                this.$refs.dialogSmsVerify.show().then((data) => {
                    alert('短信验证码验证成功...');
                    this.$router.push({ name: 'LifeResult', query: { p: -4 } });
                }, (data) => {
                });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] !== from.meta['groupName'] && to.meta['groupName'] !== 'selector') {
                this.$ls.removeItem('power');
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>
    .select-account {
        background-color: #fff;
    }
</style>


