<template>
    <div class="traffic">
        <div class="field-part">
            <mt-field label="处罚决定书编号" v-model="illegal_no"></mt-field>
        </div>

        <div class="field-reminder">
            <h4>
                温馨提示：
            </h4>
            <p>
                1、通过电子渠道缴费暂不支持缴费凭证打印功能，如需缴费凭证请至柜面办理缴费业务。
            </p>
            <p>
                2、该缴费业务只支持安徽省内的交通罚款缴纳。
            </p>
        </div>

        <div class="field-action">
            <mt-button type="primary" :disabled="!clickable" size="large" @click="doQuery">查询</mt-button>
        </div>
    </div>
</template>

<script>
    import * as services from '@/services/life';
    export default {
        name: 'traffic',
        data() {
            return {
                illegal_no: '',
                clickable: false
            };
        },
        watch: {
            'illegal_no': 'validate'
        },
        created() {
            let ls = this.$ls.getItem('traffic');
            if (ls !== null && ls !== undefined) {
                this.illegal_no = ls.illegal_no;
            }
        },
        methods: {
            doQuery() {
                services.getTrafficInfo()
                    .then(res => {
                        console.log(res.data);
                        this.$ls.setItem('traffic', res.data);
                        this.$router.push({ name: 'LifeTrafficInfo' });
                    });
            },
            validate(val) {
                if (this.$validate.isEmpty(val)) {
                    this.clickable = false;
                    return false;
                }
                this.clickable = true;
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] === from.meta['groupName'] || to.meta['groupName'] == 'selector') {
                let ls = this.$ls.getItem('traffic');
                let obj = Object.assign({}, { illegal_no: this.illegal_no }, ls);
                this.$ls.setItem('traffic', obj);
            } else {
                this.$ls.removeItem('traffic');
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>

</style>


