<template>
    <div class="history-detail">
        <div class="main">
            <div class="hd">
                <div class="title">
                    <img src="../../../assets/img/life/power.png" width="32" height="32">
                    <p v-text="title"></p>
                </div>
                <div class="amount">
                    <p>{{detailList.pay_amount | formatNumber(2)}}元</p>
                </div>
            </div>
            <div class="wrap">
                <cell-map :data-copy="detailList" :map="filter"></cell-map>
            </div>
        </div>
    </div>
</template>

<script>
    import * as services from '@/services/life';
    import Map from './map';
    export default {
        name: 'history-detail',
        data() {
            return {
                detailList: ''
            };
        },
        computed: {
            title() {
                if (this.detailList.type === '交通罚款') {
                    return '交通罚款';
                } else if (this.detailList.type === '话费充值') {
                    return this.detailList.carrieroperator;
                } else {
                    return this.detailList.busi_no;
                }
            },
            filter() {
                if (this.detailList.type === '交通罚款') {
                    return Map.traffic;
                } else if (this.detailList.type === '话费充值') {
                    return Map.phone;
                } else {
                    return Map.other;
                }
            }
        },
        created() {
            services.getHistoryListDetail({ showLoading: '加载中...' })
                .then(res => {
                    let data = res.data.filter(item => {
                        return item.tran_no === this.$route.params.id;
                    });
                    this.detailList = data[0];
                });
        }
    };

</script>

<style lang="scss" scoped>
    .main {
        .hd {
            padding: 60px 0 50px;
            background-color: #fff;
            .title {
                text-align: center;

                img {
                    vertical-align: middle;
                    display: inline-block;
                }

                p {
                    display: inline-block;
                    vertical-align: middle;
                    font-size: 17px;
                    color: #272727;
                }
            }

            .amount {
                margin: 20px 0 0;
                text-align: center;

                p {
                    font-size: 24px;
                    color: #FF942F;
                }
            }
        }

        .hd+.wrap {
            margin-top: 40px;
        }
    }
</style>


