<template>
    <div class="gas">
        <module-index ref="child" @validate="validateCall" module="gas" moduleCompany="燃气公司"></module-index>

        <div class="field-action">
            <mt-button type="primary" :disabled="!clickable" size="large" @click="doQuery">查询</mt-button>
        </div>
    </div>
</template>

<script>
    import ModuleIndex from '../module/index';
    import * as services from '@/services/life';
    export default {
        name: 'gas',
        data() {
            return {
                clickable: false
            };
        },
        components: {
            ModuleIndex
        },
        methods: {
            doQuery() {
                /** 获取缴费信息 **/
                services.getPowerinfo()
                    .then(res => {
                        let _data = res.data;
                        let ls = this.$ls.getItem('gas');
                        let obj = Object.assign({}, _data, ls);
                        this.$ls.setItem('gas', obj);
                        this.$router.push({ name: 'LifeGasInfo' });
                    }, error => {
                        console.log(error);
                    });
            },
            validateCall(data) {
                this.clickable = data;
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] === from.meta['groupName'] || to.meta['groupName'] == 'selector') {
                let ls = this.$ls.getItem('gas');
                let obj = Object.assign({}, this.$refs.child.life, ls);
                this.$ls.setItem('gas', obj);
            } else {
                this.$ls.removeItem('gas');
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>

</style>


