<template>
    <div class="module">
        <div class="field-part">

            <mt-cell class="mint-select" title="缴费地区" is-link>
                <select class="mint-field-core" @change="areaChange()" v-model="life.area">
                    <option v-for="item in areaList" :key="item.id" :value="item.id">
                        {{item.area}}
                    </option>
                </select>
            </mt-cell>

            <mt-cell class="mint-select" :title="moduleCompany" is-link>
                <select class="mint-field-core" @change="companyChange()" v-model="life.company">
                    <option v-for="item in companyList" :key="item.id" :value="item.id">
                        {{item.name}}
                    </option>
                </select>
            </mt-cell>

            <mt-field label="用户编号" type="number" placeholder="请输入您的用户编号" v-model="life.no"></mt-field>
        </div>

        <div class="field-reminder" v-if=" module !=='power' ">
            <h4>温馨提示：</h4>
            <p>1、业务缴费受理时间为01:00-23:00,请您合理安排缴费时间，由此带来的不便，敬请谅解！</p>
            <p>2、通过电子渠道缴费不支持发票凭证打印功能。</p>
        </div>
    </div>
</template>

<script>
    import * as services from '@/services/life';
    export default {
        name: 'module',
        props: ['module', 'moduleCompany'],
        data() {
            return {
                life: {
                    area: '',
                    areaName: '',
                    company: '',
                    companyName: '',
                    no: ''
                },
                areaList: [],
                companyList: [],
                itemMap: {
                    power: 'D',
                    traffic: 'F'
                }
            };
        },
        watch: {
            'life': {
                handler: 'validate',
                deep: true
            }
        },
        created() {
            // 缴费过程中缓存缴费信息
            let ls = this.$ls.getItem(this.module);
            if (ls !== null && ls !== undefined) {
                this.life = Object.assign({}, ls);
            }
            // 存在地区缓存直接从缓存获取
            let areaCompany = this.$ls.getItem('life-area-company');
            if (areaCompany !== null && ls !== undefined) {
                this.areaList = areaCompany.areaList;
                this.getCompany(areaCompany);
            } else {
                let type = this.itemMap[this.module];
                services.getAreaCompany({ BUSINESS_TYPE: type })
                    .then(res => {
                        let _data = res.data;
                        this.$ls.setItem('life-area-company', res.data);
                        this.areaList = res.data.areaList;
                        this.getCompany(res.data);
                    }, (err) => {
                        console.log(err);
                    });
            }
        },
        methods: {
            areaChange() {
                // 此处areaName、companyName由于考虑缓存的读取方便和一致性，不写成computed
                let _data = this.areaList.filter(item => {
                    return item.id === this.life.area;
                });
                this.life.areaName = _data[0].area;
                let ls = this.$ls.getItem('life-area-company');
                this.getCompany(ls);
            },
            companyChange() {
                let _data = this.companyList.filter(item => {
                    return item.id === this.life.company;
                });
                this.life.companyName = _data[0].name;
            },
            getCompany(val) {
                this.companyList = val.companyList.filter(item => {
                    return item.citycode === this.life.area;
                });
            },
            validate(val) {
                let _data = Object.assign({}, val);
                if (this.$validate.isEmpty(_data.area)) {
                    this.$emit('validate', false);
                    return false;
                }
                if (this.$validate.isEmpty(_data.company)) {
                    this.$emit('validate', false);
                    return false;
                }
                if (this.$validate.isEmpty(_data.no)) {
                    this.$emit('validate', false);
                    return false;
                }
                this.$emit('validate', true);
            }
        }
    };

</script>


