<template>
    <div class="school">
        <div class="field-part">

            <mt-cell class="mint-select" title="缴费地区">
                <select class="mint-field-core" @change="areaChange()" v-model="formData.area">
                    <option v-for="item in areaList" :key="item.id" :value="item.id">
                        {{item.area}}
                    </option>
                </select>
            </mt-cell>

            <mt-cell class="mint-select" title="学校名称">
                <select class="mint-field-core" @change="schoolChange()" v-model="formData.school">
                    <option v-for="item in schoolList" :key="item.id" :value="item.id">
                        {{item.name}}
                    </option>
                </select>
            </mt-cell>

            <mt-field label="学号" type="number" :attr="{ maxlength: 18 }" placeholder="请输入学号" v-model="formData.no"></mt-field>
        </div>

        <div class="field-action">
            <mt-button type="primary" size="large" @click="doQuery">查询</mt-button>
        </div>
    </div>
</template>

<script>
    import * as services from '@/services/life';
    export default {
        name: 'school',
        data() {
            return {
                formData: {
                    area: '',
                    areaName: '',
                    school: '',
                    schoolName: '',
                    no: ''
                },
                areaList: [],
                schoolList: []
            };
        },
        created() {
            let ls = this.$ls.getItem('school');
            if (ls !== null && ls !== undefined) {
                this.formData = Object.assign({}, this.$ls.getItem('school'));
            }
            // 存在地区缓存直接从缓存获取
            let areaSchool = this.$ls.getItem('life-school-area');
            if (areaSchool !== null && ls !== undefined) {
                this.areaList = areaSchool.areaList;
                this.getSchool(areaSchool);
            } else {
                services.getSchoolList()
                    .then(res => {
                        let _data = res.data;
                        this.$ls.setItem('life-school-area', res.data);
                        this.areaList = _data.areaList;
                        this.getSchool(_data);
                    }, (err) => {
                        console.log(err);
                    });
            }
        },
        methods: {
            areaChange() {
                let _data = this.areaList.filter(item => {
                    return item.id === this.formData.area;
                });
                this.formData.areaName = _data[0].area;
                let ls = this.$ls.getItem('life-school-area');
                this.getSchool(ls);
            },
            schoolChange() {
                let _data = this.schoolList.filter(item => {
                    return item.id === this.formData.school;
                });
                this.formData.schoolName = _data[0].name;
            },
            getSchool(val) {
                this.schoolList = val.schoolList.filter(item => {
                    return item.citycode === this.formData.area;
                });
            },
            doQuery() {
                let _data = Object.assign({}, this.formData);
                if (this.$validate.isEmpty(_data.area)) {
                    this.$msg('请选择缴费地区');
                    return false;
                }
                if (this.$validate.isEmpty(_data.school)) {
                    this.$msg('请选择学校名称');
                    return false;
                }
                if (this.$validate.isEmpty(_data.no)) {
                    this.$msg('请输入学号');
                    return false;
                }
                this.$router.push({ name: 'LifeSchoolInfo' });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] === from.meta['groupName'] || to.meta['groupName'] == 'selector') {
                this.$ls.setItem('school', this.formData);
            } else {
                this.$ls.removeItem('school');
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>

</style>


