<template>
    <div class="power-do-pay">
        <div class="field-part">
            <slot name="item"></slot>
        </div>

        <div class="field-part">
            <mt-cell title="支付金额">{{infoList.money | formatNumber(2)}}元</mt-cell>
        </div>

        <div class="field-part">
            <!--<cell title="支付账户" @click.native="showAccount()" v-model="formData.account" isLink select>
                <div slot="desc" v-if="formData.account_balance" class="account-balance">
                    可用余额<span>{{formData.account_balance}}</span>元
                </div>
            </cell>-->
            <account-select title='支付账号' :value='formData.account' :balance="formData.account_balance" @click.native="showAccount()"></account-select>
            <selector-payment-account ref="selectorPaymentAccount" :options=" selectorPaymentAccount "></selector-payment-account>
        </div>

        <div class="field-part">
            <mt-field label="交易密码" v-model="formData.pwd"></mt-field>
        </div>

        <div class="field-part">
            <mt-field label="备注名称" v-model="formData.mark"></mt-field>
        </div>

        <div class="field-hint">
            {{warn}}
        </div>
    </div>
</template>

<script>
    export default {
        name: 'power-do-pay',
        props: {
            warn: [String],
            module: [String]
        },
        data() {
            return {
                infoList: {},
                formData: {
                    account: '',
                    account_balance: '',
                    pwd: '',
                    mark: ''
                },
                selectorPaymentAccount: {
                    checked: {},
                    showAddBtn: true,
                    showWarn: true,
                    accountList: [
                        {
                            id: 4,
                            name: '郭振宇',
                            alias: '郭振宇',
                            account: '625228***0089',
                            account_balance: '45000.67',
                            bankName: '安徽农金'
                        },
                        {
                            id: 5,
                            name: '郭振宇2',
                            alias: '郭振宇2',
                            account: '625228***0025',
                            account_balance: '23000.67',
                            bankName: '安徽农金'
                        },
                        {
                            id: 6,
                            name: '郭振宇3',
                            alias: '',
                            account: '625228***0109',
                            account_balance: '26000.67',
                            bankName: '安徽农金'
                        }
                    ]
                }
            };
        },
        created() {
            let ls = this.$ls.getItem(this.module);
            this.infoList = Object.assign({}, ls);
        },
        methods: {
            showAccount() {
                this.$refs.selectorPaymentAccount.show().then((data) => {
                    this.selectorPaymentAccount.checked = data.item;
                    this.formData.account = this.selectorPaymentAccount.checked.account;
                    this.formData.account_balance = this.selectorPaymentAccount.checked.account_balance;
                }, (data) => {
                });
            }
        }
    };

</script>

<style lang="scss" scoped>
    .select-account {
        background-color: #fff;
    }

    .account-balance {
        font-size: 13px;
        color: #666666;
        letter-spacing: 0;
        margin-top: 16px;

        span {
            color: #FF942F;
            padding: 0 10px;
        }
    }
</style>


