<template>
    <div class="history">
        <div v-for="list in map" :key="list.key" v-if="infoList[list.key].length > 0">
            <div class="cell-top">
                {{list.title}}
            </div>
            <cell v-for="(item, index) in infoList[list.key]" class="history-cell" @click.native="doDetail(item)" ref="cell" :key="index"
                :title="item.type" :label="setValue(item)" :hdTitle="setWeek(item)" :hdDesc="setDate(item)" isLink>
                <span slot="value">{{item.amount | formatNumber}}元</span>
                <img src="../../../assets/img/life/power.png" slot="icon" width="24" height="24">
            </cell>
        </div>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui-rem-dpr';
    import * as services from '@/services/life';
    export default {
        name: 'history',
        data() {
            return {
                cellList: null,
                map: [
                    { key: '12', title: '十二月' },
                    { key: '11', title: '十一月' },
                    { key: '10', title: '十月' },
                    { key: '9', title: '九月' },
                    { key: '8', title: '八月' },
                    { key: '7', title: '七月' },
                    { key: '6', title: '六月' },
                    { key: '5', title: '五月' },
                    { key: '4', title: '四月' },
                    { key: '3', title: '三月' },
                    { key: '2', title: '二月' },
                    { key: '1', title: '一月' }
                ],  // 使用数组控制顺序
                infoList: {
                    1: [],
                    2: [],
                    3: [],
                    4: [],
                    7: [],
                    6: [],
                    5: [],
                    8: [],
                    9: [],
                    10: [],
                    11: [],
                    12: []
                }
            };
        },
        created() {
            services.getHistoryList({ showLoading: '加载中...' })
                .then(res => {
                    this.cellList = res.data;
                    res.data.filter(item => {
                        let m = new Date(item.date).getMonth();
                        this.infoList[m + 1].push(item);
                    });
                });
        },
        methods: {
            setValue(item) {
                let mark = item.remark || "";
                let name = item.name;
                if (item.type === '话费充值') {
                    name = item.carrieroperator;
                }
                return mark === "" ? name : mark + "  |  " + name;
            },
            setWeek(item) {
                let now = new Date();
                let weekday = new Array(7);
                weekday[0] = "周日";
                weekday[1] = "周一";
                weekday[2] = "周二";
                weekday[3] = "周三";
                weekday[4] = "周四";
                weekday[5] = "周五";
                weekday[6] = "周六";
                return weekday[new Date(item.date).getDay()];
            },
            setDate(item) {
                let now = new Date();
                return new Date(item.date).format('MM-dd');
            },
            doDetail(item) {
                this.$router.push({ name: 'LifeHistoryDetail', params: { id: item.tran_nos } });
            }
        }
    };

</script>

<style lang="scss" scoped>
    .cell-top {
        margin: 16px 0 20px 50px;
        font-size: 13px;
        color: #666;
    }
</style>;


