<template>
    <div class="result">
        <div class="field-result">
            <div class="result-icon">
                <i class="mintui mintui-success"></i>
            </div>

            <div class="result-name" v-text="call.status"></div>
            <div class="result-msg" v-text="call.msg"></div>
            <div class="result-msg next" v-text="call.msgNext"></div>
        </div>

        <div class="field-action">
            <mt-button type="primary" size="large" @click="finish">完成</mt-button>
        </div>
    </div>
</template>

<script>
    import * as services from '@/services/life';
    export default {
        name: 'result',
        data() {
            return {
                call: {
                    icon: '',
                    status: '交易成功',
                    msg: '缴费申请已受理',
                    msgNext: '具体到账时间以合作方系统为主'
                }
            };
        },
        methods: {
            finish() {
                this.$router.go(this.$route.query.p);
            }
        }

    };

</script>

<style lang="scss" scoped>
    .field-result {
        text-align: center;
        background-color: #fff;
        div+div {
            margin-top: 40px;
        }
        .result-icon {
            padding-top: 48px;
            font-size: 16px;
        }
        .result-name {
            font-size: 17px;
            color: #272727;
        }
        .result-msg {
            font-size: 13px;
            color: #B5B5B5;
        }

        .next {
            margin-top: 0;
            padding-bottom: 48px;
        }
    }
</style>


