<template>
    <div class="school-info">
        <div class="field-part field-list" v-for="item in infoList" v-if="infoList !== null">
            <div @click="pay(item)">
                <mt-cell title="学校名称" :value="item.schoolName"></mt-cell>
                <mt-cell title="学号" :value="item.no"></mt-cell>
                <mt-cell title="姓名" :value="item.name"></mt-cell>
                <mt-cell title="班级" :value="item.grade"></mt-cell>
                <mt-cell title="缴费总额" :value="item.total_money"></mt-cell>
            </div>
            <cell title="缴费明细" @click.native="showDetail(item)" isLink select></cell>
            <!-- 缴费明细 -->
            <transition-group name="pay-detail">
                <template v-for="(list, key) in item.detail" v-if="detail === item.id">
                    <mt-cell :key="key" :title="selcetList[key]" :value="list"></mt-cell>
                </template>
            </transition-group>
            <!-- 类别 -->
            <mt-cell class="mint-select" title="类别" is-link>
                <select @change="onChange(item)" class="mint-field-core" v-model="select[item.id]">
                    <option v-for="(val, key) in item.detail" key="key" :value="key">
                        {{selcetList[key]}}
                    </option>
                </select>
            </mt-cell>

            <mt-field label="缴费金额" v-model="item.money" readonly disableClear></mt-field>

        </div>
    </div>
</template>

<script>
    import * as services from '@/services/life';
    export default {
        name: 'school-info',
        data() {
            return {
                infoList: {},
                selcetList: {
                    book_amount: '书费',
                    learn_amount: '学费',
                    stay_amount: '住宿费',
                    derate_amount: '减免费用',
                    insurance_amount: '保险费用'
                },
                select: {}, // select里面包含动态值
                detail: null    // 显示哪一条缴费明细
            };
        },
        created() {
            /** 获取缴费信息数据 **/
            services.getSchoolInfo()
                .then(res => {
                    this.infoList = res.data;
                }, err => {
                    console.log(err);
                });
        },
        methods: {
            showDetail(item) {
                this.detail = this.detail === null ? item.id : null;
            },
            onChange(item) {
                let key = this.select[item.id];
                this.$set(item, 'money', item.detail[key]);
            },
            pay(item) {
                let ls = this.$ls.getItem('school');
                let obj = Object.assign({}, ls, item);
                this.$ls.setItem('school', obj);
                this.$router.push({ name: 'LifeSchoolPay' });
            }
        }
    };

</script>

<style lang="scss" scoped>
    .field-list {
        margin: 20px;
        background-color: #fff;

        .info-list {
            background-color: #ccc;
        }

        .pay-detail-enter-active {
            animation: fadein .6s;
        }

        .pay-detail-leave-active {
            animation: fadeout .6s;
        }

        @keyframes fadein {
            0% {
                transform: scaleY(0);
                opacity: 0;
            }

            100% {
                transform: scaleY(1);
                opacity: 1;
            }
        }

        @keyframes fadeout {
            0% {
                transform: scaleY(1);
                opacity: 1;
            }
            100% {
                transform: scaleY(0);
                opacity: 0;
            }
        }
    }
</style>


