<template>
    <div class="traffic-info">
        <div class="field-part">
            <cell-map :dataCopy="infoList" :map="map"></cell-map>
        </div>

        <div class="field-action">
            <mt-button type="primary" size="large" @click="next">立即缴费</mt-button>
        </div>

    </div>
</template>

<script>
    import * as services from '@/services/life';
    export default {
        name: 'traffic-info',
        data() {
            return {
                infoList: null,
                map: [
                    {
                        key: 'person',
                        title: '当事人'
                    },
                    {
                        key: 'driver_card_No',
                        title: '驾驶证编号',
                        divide: true
                    },
                    {
                        key: 'code',
                        title: '违章代码'
                    },
                    {
                        key: 'date',
                        title: '处罚日期',
                        divide: true
                    },
                    {
                        key: 'illegal_amount',
                        type: 'amount-unit',
                        title: '违法金额'
                    },
                    {
                        key: 'lata_fee',
                        title: '滞纳金',
                        type: 'amount-unit',
                        divide: true
                    },
                    {
                        key: 'total_amount',
                        type: 'amount-unit',
                        title: '缴纳总金额'
                    }
                ]
            };
        },
        created() {
            let ls = this.$ls.getItem('traffic');
            this.infoList = ls;
        },
        methods: {
            next() {
                this.$router.push({ name: 'LifeTrafficPay' });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] === from.meta['groupName'] || to.meta['groupName'] == 'selector') {
                let ls = this.$ls.getItem('traffic');
                let obj = Object.assign({}, this.infoList, ls);
                this.$ls.setItem('traffic', obj);
            } else {
                this.$ls.removeItem('traffic');
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>

</style>


