<template>
    <div class="television">
        <module-index ref="child" @validate="validateCall" module="television" moduleCompany="有线电视公司"></module-index>

        <div class="field-action">
            <mt-button type="primary" :disabled="!clickable" size="large" @click="doQuery">查询</mt-button>
        </div>
    </div>
</template>

<script>
    import ModuleIndex from '../module/index';
    import * as services from '@/services/life';
    export default {
        name: 'television',
        data() {
            return {
                clickable: false
            };
        },
        components: {
            ModuleIndex
        },
        methods: {
            doQuery() {
                /** 获取缴费信息 **/
                services.getPowerinfo()
                    .then(res => {
                        let _data = res.data;
                        let ls = this.$ls.getItem('television');
                        let obj = Object.assign({}, _data, ls);
                        this.$ls.setItem('television', obj);
                        this.$router.push({ name: 'LifeTelevisionInfo' });
                    }, error => {
                        console.log(error);
                    });
            },
            validateCall(data) {
                this.clickable = data;
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] === from.meta['groupName'] || to.meta['groupName'] == 'selector') {
                let ls = this.$ls.getItem('television');
                let obj = Object.assign({}, this.$refs.child.life, ls);
                this.$ls.setItem('television', obj);
            } else {
                this.$ls.removeItem('television');
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>

</style>


