<template>
    <div class="manager">
        <div class="field-part">
            <cell-map :dataCopy="formData" :map="map"></cell-map>
        </div>
        <div class="field-part">
            <mt-field label="备注名称" v-model="formData.mark"></mt-field>
        </div>
        <div class="field-warn">
            备注名称例如：妈妈家的电费
        </div>
        <!-- 按钮 -->
        <div class="field-action">
            <mt-button type="default" size="large" @click="doSave">保存</mt-button>
        </div>
    </div>
</template>

<script>
    import * as services from '@/services/life';
    export default {
        name: 'manager',
        data() {
            return {
                formData: {
                    name: ''
                },
                map: [
                    {
                        key: 'companyName',
                        title: '缴费公司'
                    },
                    {
                        key: 'no',
                        title: '用户编号'
                    },
                    {
                        key: 'name',
                        title: '用户姓名'
                    }
                ]
            };
        },
        created() {
            this.formData = Object.assign({}, this.$route.params.info);
        },
        methods: {
            doSave() {
                this.$router.go(-1);
            }
        }
    };

</script>

<style lang="scss" scoped>

</style>


