<template>
    <div class="school-pay">
        <div class="field-part field-list" v-if="infoList !== null">
            <mt-cell title="学校名称" :value="infoList.schoolName"></mt-cell>
            <mt-cell title="学号" :value="infoList.no"></mt-cell>
            <mt-cell title="姓名" :value="infoList.name"></mt-cell>
            <mt-cell title="班级" :value="infoList.grade"></mt-cell>
            <mt-cell title="缴费总额" :value="infoList.total_money"></mt-cell>
            <cell title="缴费明细" @click.native="showDetail(infoList)" isLink select></cell>
            <transition-group name="pay-detail">
                <template v-for="(item, key) in infoList.detail" v-if="detail === infoList.id">
                    <mt-cell class="info-list" :key="key" :title="selcetList[key]" :value="item"></mt-cell>
                </template>
            </transition-group>
            <mt-cell class="mint-select" title="类别" is-link>
                <select class="mint-field-core" v-model="select">
                    <option v-for="(item, key) in infoList.detail" :key="key" :value="key">
                        {{selcetList[key]}}
                    </option>
                </select>
            </mt-cell>
            <mt-field label="缴费金额" readonly disableClear v-model="infoList.detail[select]"></mt-field>
        </div>

        <div class="field-part select-account">
            <cell title="支付账户" @click.native="showAccount()" v-model="formData.account" isLink select>
                <div slot="desc" class="account-balance" v-if="formData.balance" v-text="'可用余额' + formData.balance + '元'"></div>
            </cell>
            <selector-payment-account ref="selectorPaymentAccount" :options=" selectorPaymentAccount "></selector-payment-account>
        </div>

        <div class="field-part">
            <mt-field label="交易密码" v-model="formData.pwd"></mt-field>
        </div>

        <div class="field-action">
            <mt-button type="primary" size="large" @click="doPay">立即支付</mt-button>
        </div>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui-rem-dpr';
    import * as services from '@/services/life';
    export default {
        name: 'school-pay',
        data() {
            return {
                infoList: {},
                selcetList: {
                    book_amount: '书费',
                    learn_amount: '学费',
                    stay_amount: '住宿费',
                    derate_amount: '减免费用',
                    insurance_amount: '保险费用'
                },
                select: '',
                detail: null,
                formData: {
                    account: '',
                    balance: '',
                    pwd: ''
                },
                selectorPaymentAccount: {
                    title: '选择付款账号',
                    checked: {},
                    showAddBtn: true,
                    accountList: [
                        {
                            id: 4,
                            name: '郭振宇',
                            alias: '郭振宇',
                            account: '625228***0089',
                            balance: '400.47',
                            bankName: '安徽农金'
                        },
                        {
                            id: 5,
                            name: '郭振宇2',
                            alias: '郭振宇2',
                            account: '625228***0025',
                            balance: '6234.09',
                            bankName: '安徽农金'
                        },
                        {
                            id: 6,
                            name: '郭振宇3',
                            alias: '',
                            account: '625228***0109',
                            balance: '20000.65',
                            bankName: '安徽农金'
                        }
                    ]
                }
            };
        },
        created() {
            this.infoList = this.$ls.getItem('school');
        },
        methods: {
            showAccount() {
                this.$refs.selectorPaymentAccount.show().then((data) => {
                    this.selectorPaymentAccount.checked = data.item;
                    this.formData.account = this.selectorPaymentAccount.checked.account;
                    this.formData.balance = this.selectorPaymentAccount.checked.balance;
                }, (data) => {
                });
            },
            showDetail(item) {
                this.detail = this.detail === null ? item.id : null;
            },
            doPay() {
                let _data = Object.assign({}, this.formData);
                if (this.$validate.isEmpty(_data.account)) {
                    this.$msg('请选择支付账号');
                    return false;
                }
                if (this.$validate.isEmpty(_data.pwd)) {
                    this.$msg('请输入交易密码');
                    return false;
                }
                this.$router.push({ name: 'LifeResult', query: { p: -4 } });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] !== from.meta['groupName'] && to.meta['groupName'] !== 'selector') {
                this.$ls.removeItem('school');
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>
    .field-list,
    .select-account {
        background-color: #fff;
    }

    .pay-detail-enter-active {
        animation: fadein .6s;
    }

    .pay-detail-leave-active {
        animation: fadeout .6s;
    }

    @keyframes fadein {
        0% {
            transform: scaleY(0);
            opacity: 0;
        }

        100% {
            transform: scaleY(1);
            opacity: 1;
        }
    }

    @keyframes fadeout {
        0% {
            transform: scaleY(1);
            opacity: 1;
        }
        100% {
            transform: scaleY(0);
            opacity: 0;
        }
    }

    .account-balance {
        color: #e6ad57;
        margin-top: 10px;
    }
</style>


